let todoLoveMixin = {
    methods: {
        //
        async clickLove(item) {
            console.log('item', item)
            // 
            // thisthis
            if (!this.loginState) {
                this.$refs.login.openLogin()
                return
            }
            // 
            if (item.has_like) {
                --item.like_count;
                // console.log(item.has_like)
                item.has_like = false;
                try {
                    await this.$u.api.unlikeThisFeed({
                        id: item.id,
                    });
                } catch (e) {
                    console.log(e)
                } finally {
                    uni.showToast({
                        title: "",
                        icon: "success",
                        duration: 1000,
                    });
                }

            } else {
                ++item.like_count;
                item.has_like = true;
                try {
                    await this.$u.api.likeThisFeed({
                        id: item.id,
                    });
                } catch (e) {
                    console.log(e)
                } finally {
                    uni.showToast({
                        title: "",
                        icon: "success",
                        duration: 1000,
                    });
                }
            }
            //
            uni.$emit('indexFeedLoveChange', item)
            uni.$emit('myFeedLoveChange', item)
        }
    }
}

export default todoLoveMixin