const install = (Vue, vm) => {
    // 
    Vue.prototype.$u.http.setConfig({
        baseUrl: 'https://kr.cctv13.eu.org/api/v2',
        loadingText: '~',
        loadingTime: 800,
        // ......
    });

    // Token
    Vue.prototype.$u.http.interceptor.request = (config) => {
        // token
        // vuextokenuViewvuex
        // https://uviewui.com/components/globalVariable.html
        // config.header.token = vm.token;
        const token = uni.getStorageSync("token")
        config.header.Authorization = "Bearer " + token
        config.header.Accept = "applicaiton/json"
        // uViewvuex$store.state
        // config.header.token = vm.$store.state.token;

        // tokenglobalDatagetApp().globalData
        // config.header.token = getApp().globalData.username;

        // tokenStorage
        // Storage
        // const token = uni.getStorageSync('token');
        // config.header.token = token;
        //config.header.Token = 'xxxxxx';

        // urlurlthis.$u.get(url)url
        //if (config.url == '/user/login') config.header.noToken = true;
        // configreturn
        return config;
        // returnfalse
        // if(config.url == '/user/rest') return false; // 
    }

    // 
    Vue.prototype.$u.http.interceptor.response = res => {
        // console.log(": ")
        // console.log(res)
        return res
    }
}

export default {
    install
}